.carousel  {
    a {
        > div {
            @media (max-width: 767px) {
                height: 400px;
            }
            img, picture {
                object-fit: cover;
                object-position: center;
                top: 0;
                position: absolute;
                width: 100%;
            }
        }
    }
}

.carousel a {
    display: block;
    &:not(:first-child) {
        display: none;
    }
}
.slider-caption{
    position: absolute;
    top:10%;
    left: 90px;
    margin-bottom: 0;
    color: #fff;
    max-width: 340px;
    @media (max-width: 767px) {
        max-width: unset;
        width: calc(100% - 30px);
        height: 180px;
        top: 50%;
        right: unset;
        left: 50%;
        transform: translate(-50%,-50%);
        text-align: center;
    }
}

.slick-slide:not(.slick-current) .img-carousel.lazyload {
    display: none!important;
}
.slick-slide figure{
    position: absolute;
    top: 0;
}

